
.status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px;
  padding-top: 12px;
  font-size: 12px;
  font-weight: 600;
  color: #1f2937;
  background: rgba(255, 255, 255, 0.95);
  position: relative;
  z-index: 40;
}

.status-left {
  flex: 1;
}

.status-right {
  display: flex;
  gap: 8px;
  align-items: center;

  i {
    font-size: 12px;
  }
}
