
.character-status {
  flex: 1;
  overflow-y: auto;
  background: #f9fafb;
  padding: 20px;
}

.status-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.status-section {
  background: white;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.basic-info {
  text-align: center;
}

.avatar-large {
  width: 80px;
  height: 80px;
  font-size: 48px;
  margin: 0 auto 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.character-name {
  font-size: 24px;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 8px;
}

.character-bio {
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 16px;
}

.stat-item {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.stat-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: #4b5563;
  margin-bottom: 8px;
}

.stat-value {
  font-weight: 600;
  color: #1f2937;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #e5e7eb;
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #667eea 0%, #764ba2 100%);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  color: #4b5563;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }

  i {
    color: #9ca3af;
    width: 16px;
  }
}
